<template>
  <div class="regular mt-6">
    <div class="subheading grey--text">
      <h1>
        >
        <v-icon>build</v-icon>
      </h1>
    </div>

    <v-container class="my-10">
      <p class="mb-6 subtitle-1">Entre com os dados:</p>

      <v-text-field
        label="Velocidade de Trabalho [Km/h]"
        v-model="velocidade"
        type="number"
        required
      ></v-text-field>

      <v-text-field
        label="Largura de Trabalho [m]"
        v-model="largura"
        type="number"
        required
      ></v-text-field>

      <v-text-field
        label="Taxa de Aplicação [Kg/ha]"
        v-model="taxa"
        type="number"
        required
      ></v-text-field>

      <v-btn rounded @click="calcular" class="mr-4 mt-10 green white--text"
        >Calcular</v-btn
      >
      <v-btn
        rounded
        @click="limpar"
        class="mt-10 blue-grey lighten-1 white--text"
        >Limpar</v-btn
      >
    </v-container>

    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-col align="center">
          <v-card-text>
            <p>Peso para ajuste em 120 segundos:</p>
            <p class="font-weight-bold title">{{ this.result }} Kg</p>
          </v-card-text>
          <v-card-actions align="center">
            <v-spacer></v-spacer>
            <v-btn rounded @click="fecharDialog" class="error white--text"
              >Cancelar</v-btn
            >
            <v-spacer></v-spacer>
            <v-btn rounded @click="conectar" class="green white--text"
              >Enviar</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-col>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    velocidade: "",
    largura: "",
    taxa: "",
    result: "",
    dialog: false,
    rules: {
      velocidade: [
        v => !!v || "Campo obrigatório",
        v => /\d+(\.\d+)?/.test(v) || "Valor inválido"
      ]
    }
  }),
  methods: {
    limpar() {
      this.velocidade = "";
      this.largura = "";
      this.taxa = "";
    },
    calcular() {
      this.result = Number(
        (this.taxa * this.largura * 1.12) / (100 / (this.velocidade / 3.6))
      ).toFixed(4);
      this.abrirDialog();
    },
    abrirDialog() {
      this.dialog = true;
    },
    fecharDialog() {
      this.dialog = false;
    },
    conectar() {
      this.$store.state.pesoRegulagem = this.result;
      this.$router.push("/conectar");
    }
  }
};
</script>
