<template>
  <div class="conectar mt-6">
    <div class="subheading grey--text">
      <h1>
        >
        <v-icon>bluetooth_searching</v-icon>
      </h1>
    </div>

    <v-container class="my-10">
      <v-layout column>
        <v-flex xs4 md6>
          <v-card height="200" class="mb-6">
            <v-card-text>
              {{ text }}
            </v-card-text>
          </v-card>
          <v-btn
            rounded
            large
            block
            class="green my-2 white--text"
            @click="connect"
            >Buscar Dispositivo</v-btn
          >
          <v-btn
            rounded
            large
            block
            class="green my-2 white--text"
            @click="abrirDialog"
            >Enviar Peso Regulagem</v-btn
          >
        </v-flex>
      </v-layout>
    </v-container>

    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title class="green white--text">Confirme os Dados</v-card-title>
        <v-col align="center">
          <v-card-text>
            <p v-if="!edit">
              Peso Regulagem: {{ this.$store.state.pesoRegulagem }} [Kg/120s]
            </p>
            <v-text-field
              v-if="edit"
              type="number"
              label="Novo Peso Regulagem"
              v-model="pesoRegulagem"
            >
            </v-text-field>
          </v-card-text>
          <v-card-actions align="center">
            <v-spacer></v-spacer>
            <v-btn
              v-if="!edit"
              rounded
              @click="editarPesoRegulagem"
              class="blue-grey lighten-1 white--text"
              >Alterar</v-btn
            >
            <v-btn
              v-if="edit"
              rounded
              @click="cancelar"
              class="error white--text"
              >Cancelar</v-btn
            >
            <v-spacer></v-spacer>
            <v-btn
              rounded
              @click="salvarPesoRegulagem"
              class="green white--text"
              >Enviar</v-btn
            >
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-col>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    text: "Conectar Com VD-TEC",
    edit: false,
    pesoRegulagem: "",
    deviceCache: null,
    characteristicCache: null
  }),
  methods: {
    abrirDialog() {
      this.dialog = true;
    },
    fecharDialog() {
      this.dialog = false;
    },
    editarPesoRegulagem() {
      this.edit = true;
    },
    salvarPesoRegulagem() {
      this.log(
        `antigo: ${this.$store.state.pesoRegulagem}, novo: ${this.pesoRegulagem}`
      );
      this.text =
        this.text +
        "\n...Enviando ao dispositivo PR(" +
        this.pesoRegulagem +
        ")";
      this.log(this.text);
      this.$store.state.pesoRegulagem = this.pesoRegulagem;
      this.fecharDialog();
      // enviar ao device
    },
    cancelar() {
      this.edit = false;
      this.fecharDialog();
    },
    connect() {
      return (this.deviceCache
        ? Promise.resolve(this.deviceCache)
        : this.requestBluetoothDevice()
      )
        .then(device => this.connectDeviceAndCacheCharacteristic(device))
        .then(characteristic => this.startNotifications(characteristic))
        .catch(error => this.log(error));
    },
    requestBluetoothDevice() {
      this.log("Requesting bluetooth device...");

      return navigator.bluetooth
        .requestDevice({
          filters: [{ services: [0xffe0] }]
        })
        .then(device => {
          this.log('"' + device.name + '" bluetooth device selected');
          this.deviceCache = device;

          // Added line
          this.deviceCache.addEventListener(
            "gattserverdisconnected",
            this.handleDisconnection
          );

          return this.deviceCache;
        });
    },
    handleDisconnection(event) {
      let device = event.target;

      this.log(
        '"' +
          device.name +
          '" bluetooth device disconnected, trying to reconnect...'
      );

      this.connectDeviceAndCacheCharacteristic(device)
        .then(characteristic => this.startNotifications(characteristic))
        .catch(error => this.log(error));
    },
    // Connect to the device specified, get service and characteristic
    connectDeviceAndCacheCharacteristic(device) {
      if (device.gatt.connected && this.characteristicCache) {
        return Promise.resolve(this.characteristicCache);
      }

      this.log("Connecting to GATT server...");

      return device.gatt
        .connect()
        .then(server => {
          this.log("GATT server connected, getting service...");

          return server.getPrimaryService(0xffe0);
        })
        .then(service => {
          this.log("Service found, getting characteristic...");

          return service.getCharacteristic(0xffe1);
        })
        .then(characteristic => {
          this.log("Characteristic found");
          this.characteristicCache = characteristic;

          return this.characteristicCache;
        });
    },
    // Enable the characteristic changes notification
    startNotifications(characteristic) {
      this.log("Starting notifications...");

      return characteristic.startNotifications().then(() => {
        this.log("Notifications started");
      });
    },
    // Output to terminal
    log(data, type = "") {
      console.log((type ? 'type="' + type + '"' : "") + data);
    }
  }
};
</script>
