import Vue from "vue";
import Router from "vue-router";

import Conectar from "./views/Conectar";
import Regular from "./views/Regular";
import Home from "./views/Home";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/index.html",
      component: Home
    },
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/conectar",
      name: "Conectar",
      component: Conectar
    },
    {
      path: "/regular",
      name: "Regular",
      component: Regular
    }
  ]
});
